@import url("play-only-mode.css");

*:focus {
  outline: none;
}

body:not(.dark) #helpfulSearch,
body:not(.dark) .ui-autocomplete {
    background-color: #fff !important;
    color: #000 !important;
}

body:not(.dark) .ui-autocomplete li:hover {
    background-color: #ddd !important;
}

body:not(.dark) #helpfulSearchDiv {
    background-color: #f9f9f9 !important;
}

#newdropdown {
  font-size: 16px;
  background-color: #fff;
  border: 1px solid #cccccc;
  color: #000000;
  padding: 24px;
  width: 400px;
  border-radius: 8px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

#newdropdown li {
  text-align: center;
  width: fit-content;
  margin: 0 auto;
}

.new-project-title {
  line-height: normal;
  font-size: 24px;
  text-align: left;
  color: #0066FF;
}

#confirmation-message {
  font-size: 16px;
  text-align: left;
  color: black;
}

.modalBox {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  padding: 24px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  z-index: 10000;
  text-align: left;
}

.modal-title {
  color: #0066FF;
  font-size: 24px;
  margin: 0 0 16px 0;
}

.modal-message {
  color: black;
  font-size: 16px;
  margin-bottom: 24px;
}

.clear-button-container {
  display: flex;
  justify-content: flex-start;
}

.confirm-button {
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: bold;
  cursor: pointer;
  background-color: #0066FF;
  color: white;
  margin-right: 16px;
  transition: background-color 0.2s ease;
}

.confirm-button:hover {
  background-color: #023a76;
}

.cancel-button {
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
  background-color: #f1f1f1;
  color: black;
}

.cancel-button:hover {
  background-color: #afafaf;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  padding: 24px;
  background-color: #fefefe;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 4px 8px;
  border-radius: 8px;
  z-index: 10000;
  text-align: left;
}

.block-count-dropdown {
  padding: 4px !important;
  font-size: 16px;
  border: 2px solid #ccc;
  border-radius: 8px;
  background: #dedededd;
  cursor: pointer;
  width: fit-content;
  text-align: center;
}

.block-count-dropdown.expanded {
  max-height: 200px;
  overflow-y: auto;
}

.message-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

#search[type="text"] {
  width: 300px;
  box-sizing: border-box;
  position: absolute;
  background-color: white;
  background-repeat: no-repeat;
  padding: 4.5px 10px 4.5px 20px;
  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
  font-size: 24px;
  color: black;
  max-width: 100%;
}

#search:focus {
  border: 2px solid #87cefa;
}

#search.open {
  margin-top: 60px;
}

#helpfulSearchDiv{
  display: block !important;
  position: absolute;
  background-color: #f0f0f0;
  padding: 2px;
  border: 1px solid #ccc;
  width: 230px;
  z-index: 1;
}

#helpfulSearch{
  padding: 2px;
  border: 2px solid grey;
  width: 220px;
  height: 20px;
  font-size: large;
}

#crossButton {
  position: absolute;
  top: 50%;
  right: -30px;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  font-size: large;
  cursor: pointer;
}

.trash-view {
  position: relative;
  background-color: white;
  max-width: 396px;
  max-height: 200px;
  overflow-y: auto;
  font-size: 16px;
  color: black;
  border: 2px solid #87cefa;
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: left;
}

.button-container {
  position: sticky;
  display: flex;
  justify-content: space-between;
  top: 0;
  z-index: 10;
  display: flex;
  gap: 10px;
  background: #2196F3;
  margin: 0;
  padding: 5px;
  border-bottom: 1px solid #d9d9d9;
}

.trash-item {
  padding: 2px 12px;
  margin: 1px 0;
  border-radius: 4px;
  transition: background-color 0.3s;
}

.trash-item.hover {
  background-color: #d9d9d9;
}

.trash-item-icon {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  vertical-align: middle;
}

#restoreLastIcon, #restoreAllIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  cursor: pointer; 
}


.material-icons.md-48 {
  font-size: 32px;
}


.hidden {
  display: none;
}

.ui-menu {
  position: relative;
  background-color: rgba(255, 255, 255, 1);
  max-width: 396px;
  max-height: 200px;
  overflow-y: auto;
  font-size: 18px;
  border: 2px solid #87cefa;
  list-style-type: none;
}

#ui-id-1 {
  margin: 0;
  padding: 0;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  -webkit-padding-start: 0;
  list-style-type: none;
}

#ui-id-1 li {
  padding-top: 5px;
}

#ui-id-1 li img {
  margin-left: 10px;
}

#ui-id-1 li:before {
  margin-right: 5px;
  font-size: 20px;
  font-weight: bold;
  margin-left: 4px;
}

#ui-id-1 li:hover:before {
  background-color: #d0d3d4;
}

.ui-state-focus {
  background-color: #d0d3d4;
}

.scrollSearch {
  max-height: 215px;
  width: 380px;
  padding-left: 10px;
  margin-bottom: 10px;
  box-sizing: border-box;
  overflow-x: auto;
  overflow-y: auto;
}

#myProgress {
  width: 180px;
  background-color: #92b5c8;
  position: absolute;
}

#myBar {
  width: 0%;
  height: 40px;
  background-color: #ffffff;
  line-height: 30px;
}

.blink {
  animation: blink-animation 1.5s infinite;
}

@keyframes blink-animation {
  50% {
    opacity: 0;
  }
}

/*CSS for load animation*/
#load-container {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  display: block;
  background-color: #fff;
  opacity: 0.7;
  z-index: 99;
  text-align: center;
}

#messageText {
  margin-top: 200px;
  font-size: 30px;
  z-index: 100;
}

#loading-image {
  z-index: 100;
}

#popdown-palette {
  display: none;
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 60%;
  max-height: 100%;
  overflow-y: scroll;
  background: rgba(255, 255, 255, 0.85);
}

#popdown-palette:last-child {
  margin-bottom: 2.5em;
}

#popdown-palette.show {
  display: block;
}

#popdown-palette.show ~ .canvasHolder {
  filter: blur(10px);
  -webkit-filter: blur(10px);
}

#popdown-palette ul {
  display: none;
}

#popdown-palette .palette.show ul {
  display: block;
}

#popdown-palette li {
  list-style: none;
}

#popdown-palette h3 {
  border-bottom: 1px solid #444;
  width: 50%;
  padding: 0 10%;
}

#popdown-palette h2 span {
  vertical-align: 50%;
}

#popdown-palette h2 img {
  display: block;
  float: right;
}

#popdown-palette .palette.show .show-button {
  display: none;
}

#popdown-palette .palette:not(.show) .hide-button {
  display: none;
}

#popdown-palette .palette:not(.show) .popout-button {
  display: none;
}

div.back {
  display: block;
  position: fixed;
  height: 2.5em;
  width: 100%;
  top: calc(100% - 2.5em);
  background-color: #4682b4;
}

div.back h2 {
  margin-top: 0.25em;
  border-bottom: none !important;
  color: white;
}

div.back:active {
  background-color: black;
}

.canvasHolder {
  transition: 0.75s ease all;
  transform-origin: 0 0;
  position: absolute;
  top: 0;
  left: 0;
}

.canvasHolder.hide {
  display: none;
}

nav {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100;
  background-color: #8bc34a;
  height: 64px;
  width: 100%;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.16), 0px 2px 10px 0px rgba(0, 0, 0, 0.12);
  font: 32px sans;
  vertical-align: middle;
  line-height: 64px;
  color: white;
}

nav .nav-container {
  padding: 0 0px;
}

nav h1 {
  font-size: 32px;
  position: relative;
  top: -12px;
  display: inline;
}

nav imghover {
  background-color: rgba(255, 255, 255, 0.3);
}

nav imgactive {
  background-color: rgba(255, 255, 255, 0.8);
}

nav ul {
  list-style: none;
  text-align: right;
  display: inline;
}

nav ul li {
  display: inline;
  padding-right: 12px;
  /*   float: right; */
}

.nav-spacer {
  height: 96px;
}

.content {
  padding: 0px;
  margin: 0px;
  display: flex;
  flex-wrap: wrap;
}

.content li {
  display: block;
  list-style: none;
  width: 23.75%;
  float: left;
  padding-left: 1%;
}

.content li[current="true"] img.thumbnail {
  opacity: 0;
}

@media (max-width: 500px) {
  .content li {
    width: 48.5%;
    padding-left: 1%;
  }

  .canvasHolder.hide {
    transform: scale(0.485);
  }

  nav h1 {
    display: none;
  }
}

@media (max-width: 800px) and (min-width: 501px) {
  .content li {
    width: 32%;
    padding-left: 1%;
  }

  .canvasHolder.hide {
    transform: scale(0.32);
  }
}

.content li img.thumbnail {
  background: #96d3f3;
  width: 100%;
  padding: 0;
}

.content li .options,
.content li .options input {
  text-align: center;
}

#planet-iframe {
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
}

iframe,
canvas {
  overflow: clip !important;
}

.projectname {
  word-wrap: break-word;
}

img.icon {
  border-radius: 100%;
  width: 30px;
}

img.icon:hover {
  background-color: rgba(139, 195, 74, 0.8);
}

#loading-image-container {
  z-index: 89999;
}

